import React, { Component } from 'react'
import { Form, Segment, Grid, Divider, Dimmer, Loader, Message, Button, Comment } from 'semantic-ui-react'
import { connect } from 'react-redux'

import './MailCompose.less';
import { messageActions } from '../../actions'

class MailCompose extends Component {
  constructor(props) {
    super(props);
    this.groupId = props.match.params.id | null;
    this.user = props.users ? props.users.user : null
    this.props.dispatch(messageActions.getMessageGroup(this.user.username,this.groupId));
    
    this.state = {
      submitted: false
    };
    
    this.handleChange = this.handleChange.bind(this);
    this.handleSubmit = this.handleSubmit.bind(this);
    this.renderMessages = this.renderMessages.bind(this);
  }
  
  renderMessages() {
    let _this = this;
    //if(!this.groupId) {this.props.messages.items = []}
    return this.props.messages.items ? 
    this.props.messages.items.map((message,key) => 
    // <Message info>
    //   <Message.Header>{message.username}</Message.Header>
    //   <p>{message.message}</p>
    // </Message>
    <Comment>
      <Comment.Avatar as='a' src={message.username == _this.user.username ? '/images/avatar_me.png' : '/images/avatar_you.png'} />
      <Comment.Content>
        <Comment.Author>{message.username}</Comment.Author>
        <Comment.Text>
          {/* <p dangerouslySetInnerHTML={{__html: message.message}}></p> */}
          <p>{message.message}</p>
        </Comment.Text>
        <Comment.Actions>
          <Comment.Action>Flag It</Comment.Action>
        </Comment.Actions>
      </Comment.Content>
    </Comment>
    )
    :
      <Dimmer active inverted>
        <Loader inverted/>
      </Dimmer>
  }

  handleChange(e) {
      const { name, value } = e.target;
      this.setState({ [name]: value });
  }

  handleSubmit(e) {
      e.preventDefault();

      this.setState({ submitted: true });
      const { message } = this.state;
      const { dispatch } = this.props;
      let groupId = this.props.match.params.id | 0;
      let username = this.user.username;
      if (username && message) {
          let payload = {username, message, groupId}
          dispatch(messageActions.postMessageData(payload));
          // clear messageBox 
      }
  }

  onChange = (editorState) => this.setState({editorState});

  render() {
    return (
      <Segment
    //style={{ padding: '3em 0em' }}
    vertical>
      <Grid container stackable verticalAlign='middle'>
        <Grid.Row>
          <Grid.Column width={16}>
          <Comment.Group>
            {this.renderMessages()}
            <Form onSubmit={this.handleSubmit}>
            <Form.TextArea label='Message' placeholder='Type a message' name='message' onChange={this.handleChange} ref='messageBox'/>
            <Form.Button>Send</Form.Button>
          </Form>
          </Comment.Group>
          </Grid.Column>
        </Grid.Row>
      </Grid>
    </Segment>
    )
  }
}

//maps store saved state to pros
const mapStateToProps = (state) => {
	return{
		...state
	}
}


export default connect(mapStateToProps)(MailCompose);



